@use "base/variables" as v;
@use "base/mixins" as m;

.pasos {
  margin-top: v.$separacion;
  &__wave {
    margin-bottom: -1rem;
  }
  &__contenido {
    background-color: v.$primario;
    margin-top: -1px;
  }
  &__heading {
    text-align: center;
    color: v.$blanco;
    font-size: 5rem;
    margin-top: 0;

    &--descripcion {
      display: block;
      font-size: 3rem;
      color: v.$blanco;
      font-weight: 400;
    }
  }
  &__grid {
    @include m.tablet {
      @include m.grid(3, v.$separacion);
    }
  }
  &__img {
    width: 70%;
    height: auto;
    transition-property: transform;
    transition-duration: 300ms;
    margin: 0 auto;
    @include m.tablet {
      width: 100%;
    }
    &:hover {
      transform: rotate(-6deg) scale(1.2);
    }
  }
}
