@use "base/variables" as v;
@use "base/mixins" as m;

.formulario {
  &__grid {
    @include m.tablet {
      @include m.grid(2, v.$separacion);
    }
  }
  &__contenido {
    display: flex;
    background-color: v.$primario;
    padding: v.$separacion;
    flex-direction: column;
    justify-content: space-evenly;
    border-top-left-radius: 4rem;
    border-bottom-left-radius: 4rem;
  }
  &__heading {
    color: v.$blanco;
    text-align: center;
    font-size: 2.8rem;
    line-height: 1.2;
    font-weight: 900;
  }
  &__campo {
    margin-bottom: 1rem;
  }
  &__label {
    display: block;
    text-transform: uppercase;
    color: v.$blanco;
    font-size: 2.2rem;
    font-weight: 700;
    line-height: 1.2;
    margin: 1rem 0;
  }
  &__input {
    width: 100%;
    padding: 1.5rem;
    border: none;
  }
  &__submit {
    @include m.boton(v.$secundario, v.$blanco);
    @include m.tablet {
      width: 100%;
    }
  }
  &__imagen--contenedor {
    margin-top: v.$separacion;
    display: flex;
    justify-content: center;
  }
  &__imagen {
    width: 30rem;
    @include m.tablet {
      width: 100%;
    }
  }
}
