@use "base/variables" as v;
@use "base/mixins" as m;

.descarga {
  padding: 5rem 0;
  background-color: v.$grisClaro;
  &__heading {
    font-size: 3.6rem;
    text-align: center;
  }
  &__grid {
    display: flex;
    flex-direction: column;
    align-items: center;

    @include m.tablet {
      flex-direction: row;
    }
  }
  &__imagen {
    img {
      border-radius: 2rem;
      width: 70%;
      height: auto;
      object-fit: scale-down;
      transform: scale(0.7);
      transition-property: transform;
      transition-duration: 300ms;
      margin: 0 auto;
      &:hover {
        transform: rotate(-6deg) scale(0.8);
      }
    }
  }
  &__contenido {
    display: flex;
    flex-direction: column;
  }
  &__tienda {
    display: flex;
    flex-direction: column;
    gap: 4rem;
  }
}
