@use "base/variables" as v;
@use "base/mixins" as m;

.testimonial {
  display: flex;
  flex-direction: column;
  background-color: v.$secundario;
  padding: 0.5rem;
  margin-bottom: 2rem;
  border-radius: 1rem;
  color: v.$blanco;
  @include m.tablet {
    margin-bottom: 0;
    &:nth-child(1) {
      grid-column: 1/4;
    }

    &:nth-child(3) {
      grid-column: 5/6;
      grid-row: 1/3;
      margin-right: 0.5rem;
    }
    &:nth-child(4) {
      grid-column: 1/3;
    }
    &:nth-child(5) {
      grid-column: 3/5;
    }
  }
  &:last-of-type {
    margin-bottom: 0;
  }
  &__header {
    display: flex;
    flex-direction: row;
    gap: 2rem;
    align-items: center;
    justify-content: left;
  }
  &__imagen {
    margin-left: 0.5rem;
    img {
      min-width: 5rem;
      max-width: 15rem;
      min-height: 5rem;
      max-height: 15rem;
      border-radius: 50%;
      object-fit: contain;
    }
  }
  &__autor {
    font-size: 3rem;
    color: v.$blanco;
    font-weight: bold;
    margin: 2.5rem 0 0 0;
  }
  &__texto {
    font-size: 2rem;
    line-height: 1.5;
    margin: 2rem 0 0 0;
    padding-left: 4rem;
    position: relative;
    &::before {
      content: '"';
      font-size: 10rem;
      position: absolute;
      left: 0;
      top: -4rem;
      opacity: 0.2;
      font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande",
        "Lucida Sans", Arial, sans-serif;
    }
  }
  &__estrellas {
    width: auto;
    height: auto;
  }
  &__informacion {
    text-align: center;
  }
}
