@use "base/variables" as v;
@use "base/mixins" as m;

.favorito {
  background-color: v.$blanco;
  margin-bottom: 2rem;
  padding: 2rem;
  box-shadow: 0px 0px 2.6rem -0.8rem darken(v.$grisClaro, 35%);
  border-radius: 1rem;
  &__grid {
    @include m.tablet {
      display: grid;
      grid-template-columns: 1fr, 3fr;
      gap: 4rem;
      align-items: center;
    }
  }
  &__imagen {
    max-width: 20rem;
    margin: 0 auto 2rem auto;
  }
  &__contenido {
    display: flex;
    flex-direction: column;
    align-items: center;
    @include m.tablet {
      align-items: flex-start;
    }
  }
  &__estrellas {
    max-width: 15rem;
    min-height: 5rem;
    width: auto;
    height: auto;
    margin-bottom: 2rem;
  }
  &__nombre {
    margin: 0;
    color: v.$primario;
    font-size: 2.4rem;
    font-weight: 900;
  }
  &__descripcion {
    margin: 0.5rem;
    text-align: center;
    color: v.$secundario;
    line-height: 1.7;
    font-weight: 600;
    @include m.tablet {
      text-align: left;
    }
  }
}
