@use "base/variables" as v;
@use "base/mixins" as m;

.repartidores {
  &__heading {
    font-size: 3.6rem;
    font-weight: 700;

    color: v.$secundario;
  }
  &__grid {
    display: flex;
    flex-direction: column;
    padding: 0.5rem;
    margin-bottom: 5rem;
    @include m.tablet {
      @include m.grid(2, 2rem);
    }
  }
  &__imagen {
    width: 100%;
    height: 100%;
    img {
      object-fit: cover;
      margin: 0 auto;
    }
  }
  &__contenido {
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
  }
  &__texto {
    margin: 2.5rem 0 0 0;
    line-height: 1.5;
  }
  &__enlace {
    @include m.boton(v.$primario, v.$blanco);
    margin-top: 2.5rem;
    span {
      font-weight: 900;
    }
  }
}
