@use "base/variables" as v;
@use "base/mixins" as m;

.favoritos {
  background-color: v.$grisClaro;
  padding: v.$separacion;
  &__heading {
    text-align: center;
    color: v.$secundario;
    font-size: 3.6rem;
  }
  &__grid {
    @include m.tablet {
      @include m.grid(2, 2rem);
    }
  }
}
