//Import
@use "variables" as v;

html {
  font-size: 62.5%; /*!Important : de esta forma mantenemos la relacion de Rem*/
  box-sizing: border-box;
}
*,
*::before,
*::after {
  box-sizing: inherit;
}
body {
  font-family: v.$fuenteprincipal;
  font-size: 1.6rem;
  background-color: v.$blanco;
  overflow-x: hidden;
}
p {
  color: v.$negro;
  font-size: 1.6rem;
  line-height: 2;
}
.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}
a {
  text-decoration: none;
  margin: 0;
  color: v.$negro;
}
img {
  max-width: 100%;
  display: block;
}
figure {
  margin: 0;
}
ul,
li {
  list-style: none;
  padding: 0;
  margin: 0;
}
