@use "variables" as v;

//LLamamaos el media con los valores de pantalla para  telefono
@mixin telefono {
  @media (min-width: v.$telefono) {
    @content;
  }
}
//LLamamaos el media con los valores de pantalla para  tablet
@mixin tablet {
  @media (min-width: v.$tablet) {
    @content;
  }
}
//LLamamaos el media con los valores de pantalla para  desktop
@mixin desktop {
  @media (min-width: v.$desktop) {
    @content;
  }
}

//Mixin de Boton
@mixin boton($color, $texto) {
  background-color: $color;
  font-size: 2rem;
  padding: 1.2rem 3rem;
  border-radius: 1rem;
  font-weight: 700;
  display: block;
  width: 100%;
  text-align: center;
  color: $texto;
  text-decoration: none;
  transition: background-color 0.3s ease;
  border: none;

  &:hover {
    cursor: pointer;
    background-color: darken($color, 10);
  }

  @include tablet {
    width: auto;
  }
}

//Mixin de Grid
@mixin grid($columnas, $espaciado) {
  display: grid;
  grid-template-columns: repeat($columnas, 1fr);
  gap: $espaciado;
}
