@use "base/variables" as v;
@use "base/mixins" as m;

.footer {
  background-color: v.$secundario;
  &__grid {
    display: flex;
    flex-direction: column;
    align-items: center;
    @include m.tablet {
      @include m.grid(3, 3rem);
      align-items: center;
    }
  }
  &__navegacion {
    display: flex;
    flex-direction: column;
    margin: 2.5rem 0;
    h3 {
      color: v.$blanco;
      text-align: left;
      margin: 0;
      position: relative;
      &::after {
        content: "";
        position: absolute;
        width: 30%;
        height: 5%;
        bottom: 0;
        left: 0;
        background-color: v.$primario;
      }
    }
    ul {
      margin: 1.5rem 0;
    }
  }
  &__enlace {
    padding-left: 2rem;
    a {
      font-size: 1.8rem;
      line-height: 1.2;
      color: v.$blanco;
    }
  }
}
