@use "base/variables" as v;

h1,
h2,
h3 {
  font-family: v.$fuentesecundaria;
  margin: 0 0 v.$separacion 0;
  text-transform: uppercase;
  text-align: center;
  line-height: 1.5;
}
h1 {
  font-size: 4rem;
}
h2 {
  font-size: 1.8rem;
  color: v.$secundario;
  margin: v.$separacion 0;
}
.heading-blanco {
  @extend h2;

  color: v.$blanco;
  span {
    color: v.$blanco;
  }
  &::after {
    display: none;
  }
}
h3 {
  font-size: 3.2rem;
}
